import {Component, EventEmitter, Input, Output} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
@Component({
   selector: "vsan-search-input",
   styleUrls: ["search.scss"],
   templateUrl: "search.html",
})
export class SearchComponent {

   private readonly INPUT_WITH_ICON: string = "input-with-icon";

   @Input()
   inputAriaLabelKey: string;

   @Input()
   itemIconVisible: boolean = true;

   @Input()
   itemIcon: string = "search";

   @Input()
   dropdownIconVisible: boolean;

   @Input()
   placeholderText: string = VsanUiUtils.getString("vsan.common.search");

   @Output()
   filterChange: EventEmitter<string> = new EventEmitter<string>();

   @Output()
   filterFocus: EventEmitter<boolean> = new EventEmitter<boolean>();

   private _filter: string;

   get filter(): string {
      return this._filter;
   }

   @Input()
   set filter(value: string) {
      this._filter = value;
      this.filterChange.emit(value);
   }

   get inputClass(): string {
      return this.itemIcon && this.itemIconVisible ? this.INPUT_WITH_ICON : "";
   }

   focus(focused: boolean): void {
      this.filterFocus.emit(focused);
   }
}
